.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Navigation {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: row;
  background-color: #282c34;
  padding-bottom: 24px;
  button {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #61dafb;
    border-color: #282c34;
    cursor: pointer;
    height: 48px;
    padding: 12px 24px;
    font-weight: bold;
  }
}
